.schedule{
    &-grid{
        display: grid;
        grid-template-columns: repeat(auto-fit,minmax(290px,1fr));
        width: 100%;
        gap: 30px;

        background-color: #221F31;
        padding: 35px;
        border-radius: 20px;

        @media screen and (max-width:640px) {
            gap: 15px;
            padding: 15px;

            grid-template-columns: minmax(0,1fr);
        }
    }
    &-card{
        padding: 25px;
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 25px;

        background: #282439;
        border-radius: 20px;
        position: relative;
        overflow: hidden;
        &::before{
            content: '';
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            display: block;
            background:linear-gradient(0deg,rgba(#fb9649,0.3), rgba(#fb9649,0));
            z-index: 1; 
        }

        &--pink{
            &::before{
                background:linear-gradient(0deg,rgba(#7e00ff,0.3), rgba(#7e00ff,0));
            }
        }
        &--green{
            &::before{
                background:linear-gradient(0deg,rgba(#21d3b4,0.3), rgba(#21d3b4,0));
            }
        }

        &__title{
            font-size: 16px;
            color: #fff;
            font-weight: 800;
            text-transform: uppercase;
            text-align: center;

            position: relative;
            z-index: 3;
        }
        &__row{
            display: flex;
            align-items: flex-start;
            gap: 15px;

            position: relative;
            z-index: 3;
        }
    }
    &-time{
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 20px;
        &__text{
            font-size: 14px;
            color: #c1c1c2;
            font-weight: 700;
        }
        &__number{
            padding: 20px 30px;
            background: rgba(#fff,.05);
            border-radius: 20px;

            font-size: 17px;
            color: #fff;
            font-weight: 800;
            position: relative;
            &::before{
                content: '';
                width: 1px;
                height: 6px;
                display: block;

                position: absolute;
                left: calc(50% - 3px);
                bottom: -6px;
                background: rgba(#fff,.18);
            }

            @media screen and (max-width:640px) {
                padding: 10px 15px;
                font-size: 13px;
            }
        }
    }
}

.team-card{
    position: relative;
    &::before,&::after{
        content: '';
        width: 100%;
        height: calc(100% - 20px);
        display: block;

        position: absolute;
        bottom: 0;
        left: 0;
        z-index: 1;

        border-radius: 20px;
    }
    &::before{
        background:linear-gradient(160deg,#262133 10%,#9A604C);
    }
    &::after{
        background: url('../img/team-card-bg.jpg') center center/cover no-repeat;
        opacity: .5;
        z-index: 2;
    }
    &__wrapper{
        padding: 0 25px 25px 35px;
        display: flex;
        flex-direction: column;
        gap: 30px;
        position: relative;
        z-index: 3;

        @media screen and (max-width:640px) {
            padding: 0 15px 15px 20px;
        }
    }
    &__title{
        font-size: 18px;
        font-weight: 800;
        color: #Fff;
        text-transform: uppercase;
    }
    &__text{
        font-size: 13px;
        font-weight: 600;
        color: #b3afc4;

        span{
            font-weight: 800;
            color: #fff;
        }
    }
    &__content{
        display: flex;
        flex-direction: column;
        gap: 10px;
    }
    &__header{
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 10px;  
    }
    &__img{
        position: relative;
        width: 93px;
        height: 93px;
        img{
            width: 100%;
            height: 100%;
            object-fit: cover;

            border-radius: 20px;
        }
    }
}