@mixin custom-scrollbar {
    &::-webkit-scrollbar {
      width: 3px;
      height: 3px;
      background-color: #221f31;
      border-radius: 20px;
    }
  
    &::-webkit-scrollbar-thumb {
      background-color: #8d64f7;
      border-radius: 20px;
    }
  
    &::-webkit-scrollbar-track {
      background-color: #221f31;
      border-radius: 20px;
    }
  }