.card {
    &-grid {
        display: grid;
        grid-template-columns: repeat(3, minmax(0, 1fr));
        gap: var(--regular-gap);
        @media screen and (max-width:1440px) {
            grid-template-columns: repeat(auto-fit,minmax(290px,1fr));
        }
    }

    &-item {
        position: relative;
 
        &::before{
            content: '';
            position: absolute;
            z-index: 2;
            width: calc(100% - 20px);
            height: 100%;

            bottom: -6px;
            left: 10px;

            border-radius: 20px;
            background:linear-gradient(280deg,rgba(#3F409E,.4), rgba(#3F409E,0));
        }
        &--balance,&--transparent{
            &::before{
                display: none;
            }
        }
        &--transparent{
        }
        &--transparent &__wrapper{
            background: #221F32;
            border: 1px solid #27233c;
            &::before{
                display: none;
            }
        }
        &--green{
            &::before{
                background:linear-gradient(280deg,rgba(#15b07f,.4), rgba(#15b07f,0));
            }
        }
        &--green &__wrapper{
            &::before{
                background:linear-gradient(-50deg,rgba(#15b07f,0.6), rgba(#15b07f,0) 65%);
            }
        }
        &--pink{
            &::before{
                background:linear-gradient(280deg,rgba(#a800ff,.4), rgba(#a800ff,0));
            }
        }
        &--pink &__wrapper{
            &::before{
                background:linear-gradient(-50deg,rgba(#a800ff,0.6), rgba(#a800ff,0) 65%);
            }
        }
        &__wrapper {
            display: flex;
            align-items: stretch;
            justify-content: space-between;
            gap: 30px;

            background: #2A263E;

            border-radius: 20px;
            padding: 30px;
            height: 100%;

            position: relative;
            z-index: 3;
            overflow: hidden;
            &::before{
                content: '';
                z-index: -1;

                position: absolute;
                width: 100%;
                height: 100%;
                display: block;
                left: 0;
                top: 0;
                background:linear-gradient(-50deg,rgba(#3F409E,0.6), rgba(#3F409E,0) 65%);
            }
            &--start{
                justify-content: flex-start;
            }
            &--column{
                flex-direction: column;
                padding: 25px 20px;
               
            }
       
        }

        &__content {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            gap: 5px;
        }

        &__icon {
            width: 90px;
            height: 90px;
            fill: rgba(#fff,.4);

            flex-shrink: 0;

            align-self: center;
            @media screen and (max-width:1700px) {
                width: 50px;
                height: 50px;
            }
            &--small{
                width: 40px;
                height: 40px;
            }
        }

        &__number {
            font-size: 32px;
            line-height: 1.2;
            font-weight: 800;

            color: #fff;

            display: flex;
            align-items: center;
            gap: 20px;
            .icon{
                width: 40px;
                height: 40px;
                flex-shrink: 0;

                fill: #15b07f;
            }
        }

        &__title {
            font-size: 14px;
            color: #bfb9eb;
            font-weight: 600;
        }
    }
}