.aside{
    &__block{
        padding: 20px 0;
        &:not(:last-child){
            border-bottom: 1px solid rgba(#fff,.05);
        }
        &--mobile{
            @media screen and (min-width:992px) {
                display: none;
            }
        }
    }
    &-list{
        display: flex;
        flex-direction: column;
        gap: 10px;

        padding: 0 30px 0 0;
    }
    &-link{
        display: flex;
        align-items: center;
        gap: 16px;
        padding: 20px 0 20px 50px;
        position: relative;
        border-radius: 0 20px 20px 0;
        fill: #4d4669;
        &::before{
            content: '';
            position: absolute;
            left: 0;
            top: 0;
            width: 4px;
            height: 100%;
            display: block;
            background: #8d64f7;

            opacity: 0;

            transition: .3s opacity;
        }
        &:hover{
            background:linear-gradient(90deg,rgba(#4B3A7A,.6),rgba(#2E2844,.6));
           
        }
        &--active{
            background:linear-gradient(90deg,rgba(#4B3A7A,.6),rgba(#2E2844,.6));
   
            fill: #8d64f7;
            &::before{
                opacity: 1;
            }
        }
        &--active &__text{
            color: #fff;
        }
        &__icon{
            width: 27px;
            height: 27px;

            flex-shrink: 0;
            transition: .3s fill;
        }
        &__text{
            font-family: var(--main-font);
            font-size: 16px;
            font-weight: 600;
            color: #beb8d6;

            transition: .3s color;
        }
    }
}