.header{	
	border-bottom: 1px solid rgba(#fff,.05);
	position: sticky;
	top: 0;
	z-index: 12;
	&__wrapper{
		display: grid;
		grid-template-columns:300px 1fr 260px;
		grid-auto-rows:100px;

		background: #1f1c2d;

		@media screen and (max-width:991px) {
			grid-template-columns: minmax(0,1fr) 1fr;
		}
		@media screen and (max-width:640px) {
			grid-template-rows: 70px;
		}
	}

	&-burger{
		@media screen and (min-width:1441px) {
			display: none;
		}
	}
	&__logo{
		max-width: 200px;
		width: 100%;
	}
	&__block{
		display: flex;
		align-items:center;
		justify-content: center;

		width: 100%;
		background: #232033;

		padding: 20px 30px;
	}
	&__profile{
		background: #221f31;
	}
	&__row{
		padding: 20px 30px;
		display: flex;
		align-items:center;
		justify-content:space-between;
		gap:20px;
	}

	&-social{
		display: flex;
		align-items:center;
		justify-content:center;
		gap:20px;
		&--desktop{
			@media screen and (max-width:991px) {
				display: none;
			}
		}
		&__item{
			flex-shrink:0;
			width: 50px;
			height: 50px;
		}
			&__link{
				width: 100%;
				height: 100%;
				display: flex;
				align-items:center;
				justify-content: center;

				background: linear-gradient(90deg, rgba(#29253e,.52),rgba(#352e58,.52));
				border-radius: 20px;
			}		
			&__link{
				fill: #574f7e;

				&:hover{
					fill: #15b07f;
				}
			}	
			&__icon{
				width: 24px;
				height: 24px;


				transition: .3s fill;
			
			}
	}
	&__profile{
		@media screen and (max-width:991px) {
			display: none;
		}
	}
	&-profile{
		height: 100%;
		&__wrapper{
			display: flex;
			align-items: center;
			justify-content: center;
			gap:20px;

			height: 100%;
			padding: 10px 30px;
		}
		&__name{
			font-family: var(--main-font);
			font-size: 16px;
			font-weight: 700;
			color: #fff;

			padding-left: 5px;

			overflow: hidden;
			text-overflow:ellipsis;
		}
		&__quit{
			display: flex;
			align-items:center;
			justify-content: center;
			width: 20px;
			height: 20px;

			flex-shrink:0;
			fill: #403b56;

			transition: .3s fill;
			&:hover{
				fill: #fff;
			}
		}
		&__img{
			width: 55px;
			height: 55px;
			flex-shrink:0;
			img{
				width: 100%;
				height: 100%;
				object-fit:cover;
				border-radius: 20px;
			}
		}
	}
}