.profile {
    &-card {
        max-width: 370px;
        width: 100%;
        flex-shrink: 0;
        position: relative;
        border-radius: 30px;
        overflow: hidden;

        background-color: #29253D;
        @media screen and (max-width:1700px) {
            max-width: 350px;
        }
        @media screen and (max-width:991px) {
            max-width: 100%;
        }
        &::before {
            content: '';
            height: 335px;
            width: 100%;
            display: block;

            position: absolute;
            top: 0;
            left: 0;

            background: linear-gradient(rgba(#8C63F7, .2), rgba(#FF00FF, 0));
        }

        &__wrapper {
            padding: 55px 30px;
            display: flex;
            flex-direction: column;
            align-items: center;
            gap: var(--regular-gap);
            height: 100%;

            position: relative;
            z-index: 3;

            @media screen and (max-width:640px) {
                padding: 30px 15px;
            }
        }

        &__img {
            width: 164px;
            height: 164px;

            background-color: rgba(#fff, .03);
            border-radius: 20px;

            position: relative;
            display: flex;
            align-items: center;
            justify-content: center;
            &::before {
                content: '';
                display: block;
                background: radial-gradient(circle at 100% 100%, #ffffff 0, #ffffff 15px, transparent 15px) 0% 0%/20px 20px no-repeat,
                    radial-gradient(circle at 0 100%, #ffffff 0, #ffffff 15px, transparent 15px) 100% 0%/20px 20px no-repeat,
                    radial-gradient(circle at 100% 0, #ffffff 0, #ffffff 15px, transparent 15px) 0% 100%/20px 20px no-repeat,
                    radial-gradient(circle at 0 0, #ffffff 0, #ffffff 15px, transparent 15px) 100% 100%/20px 20px no-repeat,
                    linear-gradient(#ffffff, #ffffff) 50% 50%/calc(100% - 10px) calc(100% - 40px) no-repeat,
                    linear-gradient(#ffffff, #ffffff) 50% 50%/calc(100% - 40px) calc(100% - 10px) no-repeat,
                    linear-gradient(0deg, rgba(49, 45, 73, 1) 0%, #6e64f7 100%);
                border-radius: 20px;

                width: 140px;
                height: 140px;
                position: absolute;
                top: calc(50% - 70px);
                left: calc(50% - 70px);
                z-index: 1;
            }

            img{
                width: 135px;
                height: 135px;
                z-index: 2;

                border-radius: 18px;
            }
        }

        &__tags {
            display: flex;
            flex-direction: column;
            gap: 20px;
            width: 100%;
        }

        &__info{
            display: flex;
            flex-direction: column;
            gap: 20px;
        }

        &__name{
            font-family: var(--main-font);
            font-size: 18px;
            font-weight: 700;
            line-height: 1;
            color: #Fff;

            text-align: center;
        }

        &__footer {
            margin: auto 0 0 0;
        }
    }

    &-rank{
        &__title{
            font-family: var(--main-font);
            font-size: 15px;
            font-weight: 600;
            color: #cecbe7;
            line-height: 1;
            span{
                font-weight: 700;
                color:#fff;
            }
        }
        &__wrapper{
            display: flex;
            flex-direction: column;
            gap: 15px;
        }
    }

    &-rating{
        &__row{
            display: flex;
            align-items: center;
            justify-content: center;
            gap: 6px;
        }
        &__icon{
            width: 16px;
            height: 16px;
            fill: #ffc947;
        }
    }

    &-tag {
        &__row {
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 20px;

            padding: 24px 22px;
            height: 55px;
            background-color: rgba(#615a84, .14);
            border-radius: 20px;
        }

        &__title {
            font-family: var(--main-font);
            font-size: 14px;
            font-weight: 700;
            color: #918bad;
            line-height: 1.2;

            &--white {
                color: #fff;
            }

            &--green {
                color: #d2ff00;
            }
        }

        &__text {
            font-family: var(--main-font);
            font-size: 11px;
            font-weight: 600;
            color: #9c97b0;
        }
    }
}