.visit{
    &-table{
        display: flex;
        flex-direction: column;
        gap: 3px;
        &__row{
            display: grid;
            grid-template-columns: minmax(200px,1fr) minmax(0,225px) minmax(0,135px) minmax(0,160px) minmax(0,100px);
            grid-auto-rows: 60px;
            gap: 3px;

            border-radius: 10px;
            overflow: hidden;

            min-width: 650px;
            &--sticky{
                position: sticky;
                top: -30px;
                @media screen and (max-width:991px) {
                    top: -15px;
                }
            }
        }
        &__column{
            background: var(--column-color);

            font-size: 14px;
            color: #fff;
            font-weight: 700;

            display: flex;
            align-items: center;
            justify-content: center;
            text-align: center;

            padding: 10px 12px;
            &--start{
                justify-content: flex-start;
            }
            &--title{
                color: #5e5a75;
                font-weight: 500;
            }
        
        }
        &__img{
            width: 40px;
            height: 40px;
            flex-shrink: 0;
            border-radius: 10px;
        }
        &__person{
            display: flex;
            align-items: center;
            gap: 10px;
            justify-content: flex-start;

            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
        }
    }
}