:root {
    --main-font: "Inter", sans-serif;
    --regular-gap: 30px;

    @media screen and (max-width:991px) {
        gap: 15px;
    }
}

body {
    font-family: var(--main-font);
    background: #221f31;
}

.logo {
    display: flex;
    align-items: center;
    justify-content: center;

    &__img {
        width: 100%;
        height: 100%;
        object-fit: contain;
    }
}

.icon {
    width: 100%;
    height: 100%;
}

.container {
    max-width: 1470px;
    width: 100%;
    margin: 0 auto;

    padding: 0 15px;

    &--big {
        max-width: 1920px;
        padding: 0 100px;

        @media screen and (max-width:1700px) {
            max-width: 1470px;
            padding: 0 15px;
        }
    }
}


.page-wrapper {
    height: 100vh;
    min-height: 550px;
    display: flex;
    flex-direction: column;

    &__content {
        height: calc(100vh - 100px);
        min-height: 450px;

        &--login {
            height: 100%;
            background: url("../img/login-bg.png") left top/cover no-repeat;
        }
    }
}

.content {
    display: flex;
    align-items: stretch;


    @media screen and (max-width:991px) {
        flex-direction: column;
    }

    &__wrapper {
        padding: 30px;
        flex-grow: 1;

        @media screen and (max-width:640px) {
            padding: 30px 0;
        }
    }

    &__aside {
        background-color: #1f1c2d;

        max-width: 300px;
        width: 100%;
        flex-shrink: 0;
        @include custom-scrollbar;
        overflow-y: auto;
        overflow-x: hidden;
        max-height: 100%;

        @media screen and (max-width:1440px) {
            position: fixed;
            top: 100px;
            left: 0;
            z-index: 12;
            height: calc(100% - 100px);


            opacity: 0;
            visibility: hidden;

            transition: .3s visibility, .5s opacity;
            &--active{
                opacity: 1;
                visibility: visible;
            }
        }

        @media screen and (max-width:640px) {
            top: 70px;
            height: calc(100% - 70px);
        }
    }
}


.page {
    &-grid {
        display: flex;
        align-items: stretch;
        gap: var(--regular-gap);

        height: 100%;

        @media screen and (max-width:991px) {
            flex-direction: column;
        }
    }

    &-content {
        display: flex;
        flex-direction: column;
        gap: 30px;
        flex-grow: 1;
    }
}

.block {
    max-height: 100%;
    overflow: hidden;


    background-color: #1E1C2D;
    border-radius: 20px;

    @include custom-scrollbar;

    padding: 25px 20px 0px 30px;

    position: relative;

    display: flex;
    flex-direction: column;
    gap: 20px;

    @media screen and (max-width:991px) {
        padding: 15px 15px 5px 15px;
    }

    &::before {
        content: '';
        width: 100%;
        height: 100%;
        display: block;
        bottom: 0;
        left: 0;
        position: absolute;
        background: linear-gradient(0deg, rgba(#221f31, 0.8), rgba(#221f31, 0));

        visibility: hidden;
        transition: .3s visibility, .5s opacity;
        z-index: 3;
    }

    &:hover::before {
        visibility: hidden;
        opacity: 0;
    }

    &--scrolled &__content {
        &::before {
            visibility: hidden;
            opacity: 0;
        }
    }

    &__grid {
        display: grid;
        grid-template-columns: 400px minmax(0, 1fr);
        gap: 45px;
    }


    &__header {
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 15px;

        @media screen and (max-width:640px) {
            flex-direction: column;
            align-items: flex-start;
        }
    }

    &__wrapper {
        display: flex;
        flex-direction: column;
        max-height: 100%;
        gap: var(--regular-gap);

        @include custom-scrollbar;
        overflow: auto;

        padding: 10px 10px 35px 0;

        @media screen and (max-width:991px) {
            padding: 15px 15px 10px 0;
        }

        @media screen and (max-width:640px) {
            padding: 0;
        }

        .block__wrapper {
            padding-bottom: 0;
        }
    }

    &__content {
        position: relative;

        &--grid {
            display: flex;
            align-items: stretch;
            gap: 45px;

            @media screen and (max-width:991px) {
                flex-direction: column;
                gap: 15px;
            }
        }

        &--no {
            &--scroll {
                overflow: hidden;
            }
        }
    }

    &__aside {
        flex-shrink: 0;
        max-width: 400px;
        width: 100%;

        &--balance {
            max-width: 360px;
        }

        @media screen and (max-width:991px) {
            max-width: 100%;
        }
    }

    &__item {
        flex-grow: 1;

        @media screen and (max-width:991px) {
            width: 100%;
        }
    }

    &__title {
        font-size: 20px;
        font-weight: 700;
        line-height: 1.2;
        color: #ffffff;
    }

    &-aside {
        &__wrapper {
            display: flex;
            flex-direction: column;
            gap: 42px;
        }
    }

    &-grid {
        display: grid;
        grid-template-columns: repeat(2, minmax(0, 1fr));
        gap: 16px;

        &__item {
            grid-column: 1/-1;

            &--small {
                grid-column: auto;
            }
        }
    }
}

.date-input,
.country-input {
    display: block;
    position: relative;

    &__item {
        position: absolute;
        opacity: 0;
        visibility: hidden;
    }

    &__wrapper {
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 12px;

        height: 72px;
        padding: 25px 20px 25px 35px;
        font-size: 15px;
        border-radius: 20px;
        box-shadow: 0px 2px 0px 0px rgba(#fff, 0.13) inset;
        background: linear-gradient(0, #2a243e, #342f45);
        font-weight: 700;
        color: #fff;

        transition: .3s opacity;
        &:hover{
            opacity: .8;
        }
    }

    &__icon {
        width: 20px;
        height: 20px;
        fill: #8d64f7;
    }
}

.country-input {
    &__item:checked~&__wrapper {
        border: 1px solid #8d64f7;

        &::before {
            background: rgba(#8d64f7, .17);
        }
    }

    &__wrapper {
        justify-content: center;

        padding: 20px 25px;

        border: 1px solid transparent;
        position: relative;
        overflow: hidden;

        transition: .3s border;

        &::before {
            content: '';
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            display: block;
            z-index: 0;
            transition: .3s background;
        }
    }
}

.notification{
    position: relative;
    &--active &-content{
        opacity: 1;
        visibility: visible;
    }
    &--active &-link{
        fill: #15b07f;
    }
    &-content{
        padding: 16px 15px 16px 25px;
        background: rgba(#29253d,.95);
        border-radius: 20px;
        width: 350px;

        position: absolute;
        top: 0;
        right: calc(100% + 10px);
        box-shadow: 0px 14px 43px 0px rgba(25, 23, 37, 0.92);

        opacity: 0;
        visibility: hidden;

        transition: .3s visibility,.5s opacity;

        @media screen and (max-width:640px) {
            right: 0;
            top: calc(100% + 10px);
            width: 280px;
        }
        &__list{
            @include custom-scrollbar;
            max-height: 260px;
            height: 100%;

            overflow: auto;
            padding-right: 10px;
        }
    }
    &-item{
        padding: 16px 0;
        &:first-child{
            padding-top: 0;
        }
        &__wrapper{
            display: flex;
            flex-direction: column;
            gap: 10px;
        }
        &__header{
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 10px;
        }
        &__time{
            white-space: nowrap;
            flex-shrink: 0;
            font-size: 12px;
            color:#7e7593;
            font-weight: 700;
        }
        &__title{
            font-size: 14px;
            font-weight: 700;
            color:#15b07f;
        }
        &__text{
            font-size: 13px;
            color:#fff;
            font-weight: 500;
        }
    }
    &__button{
        display: flex;
        align-items: center;
        justify-content: center;

        width: 100%;
        height: 40px;
        border: 1px solid #37334c;
        border-radius: 20px;
        background: rgba(#1e1b2b,.26);

        font-size: 13px;
        text-transform: uppercase;
        color: #fff;
        font-weight: 800;
    }
}

.burger {
    width: 27px;
    height: 20px;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;

    &:hover {
        cursor: pointer;
    }
    &__item {
        position: absolute;
        top: 0;
        left: 0;

        width: 100%;
        height: 3px;

        background: #574f7e;
        border-radius: 6px;

        &:nth-child(1) {
            transition:
                0.3s transform,
                0.3s top,
                0.3s background-color;
        }

        &:nth-child(2) {
            top: calc(50% - 1.5px);

            transition:
                0.3s left,
                0.3s opacity,
                0.5s visibility,
                0.3s background-color;
        }

        &:nth-child(3) {
            top: calc(100% - 3px);

            transition:
                0.3s transform,
                0.3s top,
                0.3s background-color;
        }
    }

    &--active {
        .burger__item {
            top: 8px;

            &:nth-child(1) {
                transform: rotate(45deg);
            }

            &:nth-child(2) {
                left: -10px;
                opacity: 0;
                visibility: hidden;
            }

            &:nth-child(3) {
                transform: rotate(-45deg);
            }
        }
    }
}

.button {
    display: flex;
    align-items: center;
    justify-content: center;

    text-align: center;

    width: max-content;

    &--base {
        padding: 20px 25px;
        border-radius: 20px;

        font-size: 16px;
        line-height: 1;
        font-weight: 800;
        text-transform: uppercase;
    }

    &--full {
        width: 100%;
    }

    &--big {
        height: 74px;
        font-size: 21px;
        font-weight: 900;

        border-radius: 20px;
        line-height: 1;
        text-transform: uppercase;
    }

    &--green {
        color: #fff;
        background: linear-gradient(160deg, rgba(#263645, .4) 10%, #1B836A);

        position: relative;
        overflow: hidden;
         &::before{
            content: '';
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            display: block;

            background-color: rgba(#000,.3);
            opacity: 0;
            transition: .3s opacity;
         }
         &:hover{
            &::before{
                opacity: 1;
            }
         }
    }
}

.text {
    font-family: var(--main-font);
    color: #fff;

    &--small {
        font-size: 13px;
        font-weight: 600;
        line-height: 1.2;
    }

    &--base {
        font-size: 14px;
        font-weight: 600;
        line-height: 1.2;
    }

    &--big {
        font-size: 15px;
        font-weight: 600;
        line-height: 1.2;
    }

    &--green {
        color: #15b07f;
    }

    &--grey {
        color: #9f9bb0;

        &--dark {
            color: #827d98;
        }
    }

    &--right {
        text-align: right;

        @media screen and (max-width:640px) {
            text-align: left;
        }
    }
}

.select {
    position: relative;
    z-index: 1;
    &--active {
        z-index: 10;

    }

    &--period {
        max-width: 260px;
        width: 100%;
    }

    &--full {
        max-width: 100%;
        width: 100%;
    }

    &--big &__label {
        padding: 25px 20px 25px 35px;
        font-size: 15px;
        border-radius: 20px;
        height: 72px;
        box-shadow: 0px 2px 0px 0px rgba(#fff, 0.13) inset;
        background: linear-gradient(0, #2a243e, #342f45);

        &--active {
            border-radius: 20px 20px 0 0;
        }
    }

    &__tag {
        display: none;
    }

    &__wrapper {
        position: relative;
        width: 100%;
    }

    &__label,
    &__item {
        font-family: var(--main-font);
        font-size: 14px;
        font-weight: 700;
        color: #fff;
        padding: 15px 20px;
        background: #2a2540;
        border-radius: 10px;

        font-size: 13px;
        line-height: 1.4;
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 10px;

        transition: .3s border-radius;

        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        max-width: 100%;

        &:hover {
            cursor: pointer;
        }
    }

    &__name{
        text-overflow: ellipsis;
        overflow: hidden;
    }

    &__item {
        border-radius: 0;
        width: 100%;
        transition: .3s background-color;

        &:not(:last-child) {
            border-bottom: 1px solid rgba(#fff, .05);
        }

        span{
            text-overflow: ellipsis;
            overflow: hidden;
        }

        &:hover{
            cursor: pointer;
            background: #5e5a75;
        }
    }

    &__label {

        display: flex;
        align-items: center;
        gap: 20px;
        border-bottom: 1px solid transparent;
        transition: .3s border-color, .3s border-radius,.3s opacity;

        &:hover{
            opacity: .8;
        }
        &::after {
            content: '';
            width: 0;
            height: 0;
            display: block;
            border-left: 5px solid transparent;
            border-right: 5px solid transparent;

            border-top: 5px solid #59517c;
            transition: .3s transform;
        }

        &--active {
            border-radius: 10px 10px 0 0;
            border-color: rgba(#fff, .05);

            &::after {
                transform: rotate(180deg);
            }
        }
    }

    &__content {
        position: absolute;
        top: 100%;
        left: 0;
        width: 100%;

        border-radius: 0 0 10px 10px;
        overflow: hidden;

        display: flex;
        flex-direction: column;

        border: 1px solid var(--white-color);
        border-top: none;

        transition: .3s opacity, .1s visibility;

        &--hidden {
            opacity: 0;
            visibility: hidden;
        }
    }
}

.tabs {
    &__content {
        display: none;

        &.active {
            display: block;
        }
    }
}

.form {
    &-checkbox {
        display: flex;
        align-items: center;
        gap: 12px;
        position: relative;

        &:hover {
            cursor: pointer;
        }

        &__input {
            display: none;
        }

        &__input:checked+&__checkmark {
            &::before {
                opacity: 1;
            }
        }

        &__text {
            font-size: 14px;
            font-weight: 700;
            color: #fff;
        }

        &__checkmark {
            width: 24px;
            height: 24px;
            flex-shrink: 0;
            display: flex;
            align-items: center;
            justify-content: center;

            border-radius: 50%;
            border: 2px solid #3a364d;

            &::before {
                content: '';
                width: 9px;
                height: 9px;
                display: block;
                border-radius: 50%;
                background: #15b07f;

                opacity: 0;

                transition: .3s opacity;
            }
        }
    }

    &-input {
        height: 74px;
        background-color: #282439;
        border-radius: 20px;

        display: flex;
        flex-direction: column;
        justify-content: space-between;

        grid-column: 1/-1;

        padding: 18px 22px;
        border: 1px solid transparent;

        transition: .3s border-color;

        &--error {
            border-color: #ef375e;
            position: relative;

            &::before {
                content: attr(data-error);
                font-size: 13px;
                font-weight: 600;
                color: #ef375e;
                position: absolute;
                top: -24px;
                right: 0;
            }
        }

        &__text {
            font-size: 13px;
            font-weight: 600;
            color: #58536e;
        }

        &__input {
            font-size: 16px;
            font-weight: 700;
            color: #fff;
        }
    }
}


// promo can be in another file

.promo {
    font-family: 'Magistral', sans-serif;
    &-content{
        background: #252236;
        position: relative;
        &::before{
            content: '';
            position: absolute;
            bottom: 0;
            left: 0;

            width: 100%;
            height: 100%;
            display: block;

            background: url("../img/promo-footer.png") bottom center/cover no-repeat;
        }
    }
    &-footer{
        position: relative;
        &::before{
            content: '';
            position: absolute;
            left: 0;
            bottom: 0;
            display: block;
            width: 100%;
            height: 100%;

            background: url("../img/promo-gradient.png") left bottom/contain no-repeat;
        }
        &__wrapper{
            background: rgba(#242134,.6);

            padding: 70px 0;

            @media screen and (max-width:991px) {
                padding: 30px 0;
            }
        }
        &__row{
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 20px;
            @media screen and (max-width:991px) {
                flex-direction: column-reverse;
            }
        }
        &__copyright{
            font-size: 15px;
            font-weight: 500;
            color:#fff;
            span{
                color:#807b9a;
            }
        }
        &__logo{
            height: 33px;
        }
    }
    &-section{
        padding: 50px 0;

        &:first-child{
            padding-top: 100px;
        }
        &:last-child{
            padding-bottom: 100px;
        }

        @media screen and (max-width:991px) {
            padding: 30px 0;
            &:first-child{
                padding-top: 60px;
            }
            &:last-child{
                padding-bottom: 60px;
            }
        }

        &__header{
            display: flex;
            flex-direction: column;
            gap: 10px;
            position: relative;
            margin: 0 0 100px 0;
            
            @media screen and (max-width:991px) {
                margin: 0 0 60px 0;
            }

        }
    }
    &-hero {
        background: url("../img/promo-bg.png") top center/cover no-repeat;
        padding: 120px 0;

        @media screen and (max-width:991px) {
            padding: 80px 0;
        }
    }

    &-header {
        position: absolute;
        left: 0;
        width: 100%;
        top: 0;

        &__logo {
            height: 45px;
        }

        &__row {
            display: flex;
            align-items: center;
            gap: 70px;

            padding-top: 80px;

            @media screen and (max-width:991px) {
                padding-top: 20px;
                gap: 30px;
            }
        }

        &__button {
            margin-left: auto;
        }
    }

    &-nav {
        @media screen and (max-width:991px) {
            display: none;
        }

        &__list {
            display: flex;
            align-items: center;
            gap: 50px;
            font-size: 17px;
            font-weight: 700;
        }

        &__link {
            color: #fff;

            transition: .3s color;
            position: relative;

            &::before {
                content: '';
                width: calc(100% + 20px);
                height: 3px;
                left: -10px;
                top: -90px;
                display: block;
                position: absolute;

                background: #21cb85;
                opacity: 0;
                transition: .3s opacity;
            }

            &:hover {
                color: #21cb85;

                &::before {
                    opacity: 1;
                }
            }
        }
    }

    &-button {
        display: flex;
        align-items: center;
        gap: 15px;

        padding: 20px;
        width: max-content;
        background: rgba(#21cb85, .04);
        border-radius: 20px;
        border: 1px solid #21cb85;

        transition: .3s background;
        &__icon {
            width: 22px;
            height: 22px;
            fill: #21cb85;

            transition: .3s fill;
        }

        &__text {
            font-size: 19px;
            text-transform: uppercase;
            font-family: 'Magistral Extra', sans-serif;
            font-weight: 700;

            color: #fff;
            @media screen and (max-width:991px) {
                font-size: 16px;
            }
        }

        &:hover &__icon{
            fill: #fff;
        }

        &:hover{
            background: #21cb85;
        }
    
    }

    &__title {
        font-size: 48px;
        font-family: 'Magistral Extra', sans-serif;
        font-weight: 700;
        line-height: 1;
        color: #fff;

        text-transform: uppercase;

        span {
            color: #8d64f7;
        }

        .green{
            color:#22a973;
        }

        &--small{
            font-size: 37px;
        }

        @media screen and (max-width:991px) {
            font-size: 32px;
        }
    }

    &__subtitle{
        font-size: 20px;
        color:#fff;
        font-weight: 700;
        line-height: 1.2;
        position: relative;
        &::before{
            content: '';
            width: 90px;
            height: 3px;
            display: block;

            position: absolute;
            bottom: -33px;
            left: 0;
            
            background: #21cb85;
        }
    }

    &__desc {
        font-size: 18px;
        color: #e1deee;
        font-weight: 500;
        line-height: 1.3;
        @media screen and (max-width:991px) {
            font-size: 16px;
        }
    }
}

.hero {
    position: relative;

    &::before {
        content: '';
        width: 350px;
        height: 600px;
        display: block;

        position: absolute;
        left: 0;
        bottom: -300px;
        z-index: -1;
        background: url("../img/promo-hero.png") center center/contain no-repeat;

        @media screen and (max-width:991px) {
            display: none;
        }
    }

    &__wrapper {
        padding: 140px 0;

        @media screen and (max-width:991px) {
            padding: 60px 0;
        }
    }

    &__header {
        display: flex;
        flex-direction: column;
        gap: 30px;

        max-width: 870px;
        width: 100%;
    }

    &__content {
        display: flex;
        flex-direction: column;
        gap: 140px;

        @media screen and (max-width:991px) {
            gap: 30px;
        }
    }

    &-button {
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 20px;

        padding: 24px 27px;
        width: max-content;
        max-width: 100%;

        font-family: 'Magistral Extra', sans-serif;
        font-weight: 700;
        font-size: 19px;
        line-height: 1;
        color: #fff;
        text-transform: uppercase;
        background-color: #774ce7;

        border-radius: 20px;

        border: 1px solid #774ce7;
        transition: .3s background;
        &__icon {
            width: 28px;
            height: 28px;
            flex-shrink: 0;
            fill: #fff;

        }

        &:hover{
            background: transparent;
        }

        @media screen and (max-width:991px) {
            font-size: 16px;
            width: 100%;
        }
    }
}

.advantages {
    &__wrapper {
        display: grid;
        grid-template-columns: repeat(3, minmax(0, 1fr));
        gap: 80px;

        max-width: 1100px;
        width: 100%;
        margin: 0 auto;

        padding: 40px 90px;
        border: 1px solid rgba(#fff, .23);
        -webkit-backdrop-filter: blur(3px);

        counter-reset: advantages;

        @media screen and (max-width:991px) {
            grid-template-columns: minmax(0, 1fr);
            gap: 30px;

            padding: 20px 15px;
        }
    }

    &-item {
        display: flex;
        flex-direction: column;
        gap: 20px;
        position: relative;
        counter-increment: advantages;

        &::before {
            content: "0" counter(advantages);
            font-size: 42px;
            line-height: 1;
            color: #2c2840;
            font-weight: 700;

            position: absolute;
            left: -40px;
            top: calc(50% - 21px);
            z-index: -1;
            @media screen and (max-width:991px) {
                left: 0px;    
            }
        }

        &__icon {
            width: 40px;
            height: 40px;
            fill: #21cb85;
            position: relative;

            .icon {
                width: 100%;
                height: 100%;
            }
        }

        &__title {
            color: #21cb85;
            font-weight: 700;
            font-size: 19px;
            line-height: 1.2;
        }

        &__text {
            color: #fff;
            font-size: 14px;
            font-weight: 500;
        }

        &__content {
            display: flex;
            flex-direction: column;
            gap: 10px;
        }
    }
}

.why{
    &__header{
        position: relative;
        &::before{
            content: '';
            position: absolute;
            width: 6px;
            height: calc(100% + 4px);
            top: -2px;
            left: -30px;
            border-radius: 6px;
            display: block;

            background: #8d64f7;
        }

        @media screen and (max-width:1440px) {
            padding-left: 30px;
            &::before{
                left: 0;
            }
        }
    }
    &__wrapper{
        padding-top: 140px;
        display: flex;
        flex-direction: column;
        gap: 80px;
        @media screen and (max-width:991px) {
            gap: 50px;
            padding-top: 60px;
        }
    }
    &__content{
        max-width: 1320px;
        font-family: var(--main-font);
        font-size: 18px;
        line-height: 1.2;
        font-weight: 400;
        color: #fff;

        display: flex;
        flex-direction: column;
        gap: 30px;

        .orange{
            color:#ebc470;
        }

        @media screen and (max-width:991px) {
            gap: 15px;
            font-size: 16px;

        }
    }
}

.join{
    &-grid{
        display: grid;
        grid-template-columns: repeat(3,minmax(0,1fr));
        grid-auto-rows: 250px;
        border: 1px solid #2e2a42;

        @media screen and (max-width:991px) {
            grid-template-columns: minmax(0,1fr);
            grid-auto-rows: 180px;
        }
    }
    &-item{
        display: flex;
        flex-direction: column;
        gap: 30px;
        align-items: center;
        justify-content: center;
        position: relative;
        &:nth-child(even){
            background-color: #28253a;
        }
        &__icon{
            width: 66px;
            height: 66px;
            display: flex;
            align-items: center;
            justify-content: center;

            fill: #8d64f7;
            .icon{
                width: 100%;
                height: 100%;
            }
        }
        &__title{
            font-size: 15px;
            line-height: 1.3;
            color:#fff;
            font-weight: 700;

            max-width: 230px;
            width: 100%;

            text-align: center;
        }
    }
}

.requirements{
    display: flex;
    flex-direction: column;
    gap: 33px;
    @media screen and (max-width:991px) {
        gap: 15px;
    }
    &-item{
        display: flex;
        align-items: center;
        gap: 20px;

        padding: 16px 25px;
        border-radius: 30px;
        position: relative;
        background: #252236;
        &::after{
            content: '';
            position: absolute;
            width: 100%;
            height: 100%;
            display: block;
            left: 0;
            top: 0;
            background:linear-gradient(90deg, rgba(#22a973, 0.4), #252236 90%);
            border-radius: 30px;
            z-index: 2;
        }
        &::before{
            content: '';
            width: 10%;
            height: 100%;
            left: 0;
            top: 0;
            position: absolute;
            z-index: 1;
            display: block;
            border-radius: 30px;
        }
        &__text{
            font-size: 17px;
            color: #fff;
            font-weight: 700;
            position: relative;
            z-index: 3;
        }
        &__icon{
            width: 30px;
            height: 30px;
            flex-shrink: 0;
            fill: #22a973;
            position: relative;
            z-index: 3;
            display: block;
        }
    }
}

.calendar{
    width: 100%;
    border-radius: 20px;
    border: 1px solid #221f31;
    overflow: hidden;
    &-status{
        display: grid;
        grid-template-columns: repeat(2,minmax(0,1fr));
        gap: 40px;
        @media screen and (max-width:640px) {
            gap: 20px;
        }
        &__count{
            height: 120px;
        }
    }
    &-submit{
        grid-column: 1/-1;
        &__wrapper{
            display: flex;
            align-items: stretch;
            border-radius: 20px;

            background: #221e33;
            border: 1px solid rgba(#734cd6,.13);
            @media screen and (max-width:640px) {
                flex-direction: column;
            }
        }
        &__text{
            padding:10px 10px 10px 30px;
            font-size: 14px;
            font-weight: 600;
            flex-grow: 1;

            display: flex;
            align-items: center;
            text-align: center;
            gap: 5px;


            color: #a29caf;
            span{
                font-size: 14px;
                font-weight: 800;
                color: #fff;
            }

            @media screen and (max-width:640px) {
                width: 100%;
                justify-content: center;

                padding: 15px 10px;
            }
        }
        &__button{
            display: flex;
            align-items: center;
            justify-content: center;
            height: 65px;
            width: 180px;

            flex-shrink: 0;

            background: rgba(#8d64f7,.17);
            border: 1px solid #734cd6;
            border-radius: 20px;

            font-size: 16px;
            font-weight: 800;
            color: #ffffff;

            transition: .3s background;
            &:hover{
                cursor: pointer;
                background: rgba(#8d64f7,.3);
            }

            @media screen and (max-width:640px) {
                width: 100%;
            }
        }
    }
    &-count{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        gap: 14px;
        border-radius: 20px;
        background:linear-gradient(#348b67,rgba(#221F31,1));
        box-shadow: 0px 1px 0px 0px rgba(255, 255, 255, 0.19) inset;
        &--red{
            background:linear-gradient(#ce3960,rgba(#221F31,1));
        }
        &--red &__status{
            color:#fa5883;
        }
        &__number{
            font-size: 31px;
            line-height: 1;
            font-weight: 800;
            color: #fff;

            display: flex;
            align-items: center;
            text-align: center;
            gap: 5px;

            span{
                font-size: 16px;
                font-weight: 600;
            }
        }
        &__status{
            font-size: 13px;
            font-weight: 600;
            color: #63d5a3;
        }
    }
    &-header{
        padding: 32px;
        background: #221F31;
        border-radius: 20px;
        &__wrapper{
            display: grid;
            grid-template-columns: 225px minmax(0,1fr) 225px;
            grid-auto-rows: 90px;

            background: url("../img/calendar-header-bg.png") center center/contain no-repeat;


            @media screen and (max-width:640px) {
                grid-template-columns: minmax(0,1fr);
                grid-auto-rows: 60px;
            }
        }

        @media screen and (max-width:1440px) {
            padding: 15px;
            
        }

        @media screen and (max-width:640px) {

            background: #1f1c2d;
        }
    }
    &-content{
        padding: 30px;
        max-width: 100%;
        @include custom-scrollbar;
        display: grid;
        grid-template-columns: repeat(7,minmax(0,1fr));
        gap: 20px;
        width: 100%;

        @media screen and (max-width:640px) {
            grid-template-columns: repeat(7,minmax(70px,100px));
            gap: 10px;
            width: 700px;
            overflow: auto;
        }
        @media screen and (max-width:640px) {
            padding: 15px;
            gap: 10px;
        }
    }
    &-week{
    
    }
    &-day{
        display: flex;
        align-items: center;
        &inactive{
            opacity: .3;
            pointer-events: none;
        }
        &:hover{
            cursor: pointer;
        }
        &__wrapper{
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            gap: 10px;
            width: 100%;
            height: 100%;
    
            border-radius: 20px;
            border: 1px solid #348b67;
            background: rgba(#2e7d5b,.13);
    
            padding: 20px 0;
            position: relative;
            @media screen and (max-width:640px) {
                padding: 10px 0;
            }
            &::before{
                content: '';
                position: absolute;
                width: calc(100% - 10px);
                height: calc(100% - 10px);
                display: block;
                border-radius: 20px;

                left: 5px;
                top: 5px;
                background:linear-gradient(#348b67,rgba(#348b67,0));
                border: 1px solid #348b67;
                z-index: 1;

                opacity: 0;
                transition: .3s opacity;
            }

            @media screen and (max-width:640px) {
                border-radius: 10px;
                &::before{
                    border-radius: 5px;
                }
            }
        }
        &__input:disabled ~ &__wrapper{
            border-color: #ce3960;
            background:rgba(#c3365b,.13);
        }
        &__input:disabled ~ &__wrapper &__footer{
            color: #e94d76;
        }

        &__input:checked ~ &__wrapper &__icon{
            fill: #fff;
        }
        &__input:checked ~ &__wrapper{
            &::before{
                opacity: 1;
            }
        }

        &__header,&__icon,&__footer{
            position: relative;
            z-index: 2;
        }
      
        &__header{
            font-size: 18px;
            font-weight: 800;
            color: #fff;
            text-align: center;
            span{
                font-size: 13px;
                font-weight: 600;
            }

            @media screen and (max-width:640px) {
                font-size: 13px;
                span{
                    font-size: 9px;
                }
            }
        }
        &__icon{
            width: 15px;
            height: 15px;
            fill: transparent;

            transition: .3s fill;
            @media screen and (max-width:640px) {
                width: 10px;
                height: 10px;
            }
        }
        &__footer{
            color: #489e7c;
            font-size: 13px;
            font-weight: 600;
            @media screen and (max-width:640px) {
                font-size: 9px;
            }
        }
    }
    &__mounth{
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 25px;
        background: #2a263c;
        border-radius: 20px;

        font-size: 14px;
        font-weight: 700;
        line-height: 1;

        color: #Fff;

        .icon{
            width: 30px;
            height: 30px;

            fill: #8d64f7;
        }

        @media screen and (max-width:1440px) {
            gap: 10px;
        }
        &--next{
            transition: .3s transform;
            &:hover{
                transform: translateX(10px);
                cursor: pointer;
            }
            .icon{
                transform: rotate(180deg);
            }
        }
        &--prev{
            transition: .3s transform;
            &:hover{
                transform: translateX(-10px);
                cursor: pointer;
            }
        }
        &--current{
            font-size: 17px;
            font-weight: 700;
        }
        span{
            color: #58546b;
        }
        &--current{
            background: transparent;
        }
    }
}