.payments {
    &-nav {
        &__link {
            display: flex;
            align-items: center;
            gap: 20px;

            font-size: 16px;
            color: #fff;
            font-weight: 700;

            height: 70px;

            padding: 0 0 0 25px;

            position: relative;
            transition: .3s opacity;
            &:hover{
                opacity: .8;
            }
            &::before {
                content: '';
                position: absolute;
                left: 0;
                top: 0;
                width: calc(100% + 45px);
                height: 100%;
                z-index: 0;

                opacity: 0;
                background: linear-gradient(70deg, rgba(75, 58, 122, 0.8), #221F32 50%);
                border-radius: 30px 0 0 30px;
                transition: .3s opacity;

                @media screen and (max-width:991px) {
                    width: 100%;
                    border-radius: 30px;
                }
            }

            &.active {

                &::before {
                    opacity: 1;
                }
            }

            @media screen and (max-width:991px) {
                padding: 0 15px;
            }
        }

        &__link.active &__arrow {
            &::before {
                border-color: #fff;
            }
        }

        &__icon,
        &__arrow,
        &__text {
            position: relative;
            z-index: 2;
        }

        &__icon {
            width: 25px;
            height: 25px;
            fill: #8d64f7;
        }

        &__arrow {
            width: 27px;
            height: 27px;
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 50%;
            flex-shrink: 0;
            background-color: #29253b;

            margin-left: auto;

            &::before {
                content: '';
                border: solid #827d98;
                border-width: 0 3px 3px 0;
                display: inline-block;
                padding: 3px;
                transform: rotate(-45deg);

                transition: .3s border-color;
            }
        }
    }

    &-tabs {
        &__content {
            background: #221F32;

            border-radius: 30px;
            width: 100%;
            height: 100%;

            &.block__wrapper {
                padding: 20px 25px;

                @media screen and (max-width:640px) {
                    padding: 15px;
                }

            }

        }
    }

    &-table {
        display: flex;
        flex-direction: column;
        gap: 13px;

        &__column {
            position: relative;
            z-index: 2;

            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;

            &--icon {
                flex-shrink: 0;
            }
        }

        &__title {
            text-overflow: ellipsis;
            overflow: hidden;
        }

        &__row {
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 10px;

            padding: 10px 26px 10px 20px;
            background-color: #272437;
            border-radius: 30px;

            height: 55px;

            &--title {
                background: transparent;
            }

            &:not(&--title) {
                position: relative;

                &::before {
                    content: '';
                    position: absolute;
                    width: 0;
                    height: 100%;
                    display: block;
                    left: 0;
                    top: 0;
                    background: linear-gradient(90deg, rgba(#15b07f, 0.4), rgba(#272437, 1) 60%);
                    opacity: 0;
                    z-index: 2;

                    transition: .3s width, .3s opacity;
                    border-radius: 30px;
                }

                &:after {
                    content: '';
                    width: 50%;
                    height: 100%;
                    left: 0;
                    position: absolute;
                    top: 0;
                    border-radius: 30px;
                    z-index: 1;
                    box-shadow: -11px 0px 40px 0px rgba(21, 176, 127, 0.2);
                    opacity: 0;

                    transition: .5s opacity;
                }

                &:hover::before {
                    opacity: 1;
                    width: 100%;
                }

                &:hover::after {
                    opacity: 1;
                }
            }
        }

        &__row:hover &__icon {
            fill: #fff;
        }

        &__title {
            font-size: 17px;
            font-weight: 800;
            color: #fff;
        }

        &__icon {
            width: 24px;
            height: 24px;
            display: flex;
            align-items: center;
            justify-content: center;

            flex-shrink: 0;
            fill: #15b07f;

            .icon {
                width: 100%;
                height: 100%;

                transition: .3s fill;
            }
        }
    }
}