.event{
    &-list{
        display: flex;
        flex-direction: column;
        gap: 20px;
    }
    &-item{
        padding: 30px;
        background-color: #29253d;
        border-radius: 20px;

        display: flex;
        flex-direction: column;
        gap: 20px;
        &__header{
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 15px;
        }
        &__time{
            font-size: 12px;
            font-weight: 600;
            line-height: 1;
            color: #716b8c;
        }
        &__title{
            font-size: 14px;
            line-height: 1.2;
            font-weight: 800;
            color: #ffc947;
            span{
                font-size: 11px;
                font-weight: 600;
                color: #8d86ac;
            }
        }
    }
}