.balance{
    &-nav{
        &__wrapper{
            display: flex;
            align-items: center;
        }
        &__item{
            flex-shrink: 0;
        }
        &__item:first-child &__link{
            border-bottom-left-radius: 30px;
        }
        &__link{
            padding: 20px;
            display: flex;
            align-items: center;
            font-size: 15px;
            font-weight: 700;
            color: #fff;

            position: relative;
            transition: .3s background;
            overflow: hidden;
            transition: .3s opacity;
            &:hover{
                opacity: .7;
            }
            &::before{
                content: '';
                position: absolute;
                width: 100%;
                height: calc(100% + 30px);
                display: block;

                bottom: -30px;
                left: 0;
                opacity: 0;
                background: linear-gradient(0,rgba(#ffc466,.3), rgba(#ffc466,.01));
                border-radius: 20px;
                transition: .3s opacity;
            }
            &.active{
                &::before{
                    opacity: 1;
                }
            }
        }
    }
    &-tabs{
        gap: 0px;
    }
    &-table{
        &__row{
            display: grid;
            grid-template-columns: 1fr 3fr 3fr 2fr 2fr 25px;
            min-width: 800px;
            grid-auto-rows: 44px;
            
            padding: 0px 10px 0px 20px;
            &:nth-child(even){
                background-color: #272438;
                border-radius: 40px;
            }
            &--title{
                position: sticky;
                top: -15px;
                background-color: #221F32;
            }
        }
        &__column{
            display: flex;
            align-items: center;
            justify-content: center;
            text-align: center;
            &--start{
                justify-content: flex-start;
            }
        }
        &__link{
            width: 25px;
            height: 25px;
            border-radius: 50%;
            background-color: #1f1c2d;
            display: flex;
            align-items: center;
            justify-content: center;

            position: relative;
            &::before,&::after{
                content: '';
                position: absolute;
                width: 11px;
                height: 3px;
                display: block;
                background-color: #15b07f;

                left: 50%;
                top: 50%;

                transform: translate(-50%,-50%);

                transition: .3s background;
            }
            &::after{
                transform: translate(-50%,-50%) rotate(90deg);
            }
            &:hover{
                &::before,&::after{
                    background: #fff;
                }
            }
        }
    }
    &-tabs{
        width: 100%;
        &__content{
            padding: 30px;
            border-radius: 30px;
            background-color: #221F32;

            @media screen and (max-width:640px) {
                padding: 30px 15px;
            }
        }
    }
}
