.login {
    height: 100%;
    display: flex;
    align-items: center;

    padding: 60px 0;
    &__wrapper {
        display: flex;
        flex-direction: column;
        justify-content: space-around;

        gap: 20px;
        max-height: 750px;
        height: 100%;
    }

    &__logo {
        width: 200px;
    }

    &__subtitle {
        text-transform: uppercase;
        font-size: 18px;
        font-weight: 800;
        color: #524e66;
        line-height: 1.2;
    }

    &__desc {
        display: flex;
        flex-direction: column;
        gap: 20px;
    }

    &__title {
        font-size: 39px;
        color: #fff;
        font-weight: 700;
        line-height: 1.2;
        @media screen and (max-width:640px) {
            font-size: 32px;
        }
    }

    &__content {
        display: flex;
        flex-direction: column;
        gap: 40px;
        @media screen and (max-width:640px) {
            gap: 15px;
        }
    }

    &__link {
        font-size: 14px;
        font-weight: 600;
        color: #524e66;

        a {
            color: #8d64f7;
            transition: .3s opacity;
        }

        &:hover{
            a{
                opacity: .8;
            }
        }
    }

    &__content {
        margin: 0 0 45px 0;
        @media screen and (max-width:640px) {
            margin: 0 0 20px 0;
        }

    }

    &-form {
        &__wrapper {
            max-width: 600px;
            width: 100%;
            display: grid;
            grid-template-columns: repeat(2, minmax(0, 1fr));
            gap: 35px;

            @media screen and (max-width:640px) {
            }
        }
        &__item{
            grid-column: 1/-1;
            &--small{
                grid-column: auto;
                @media screen and (max-width:640px) {
                    grid-column: 1/-1;
                }
            }
        }
    }
}